
* {
  margin: 0;
  padding: 0;
}
body {
  background: #f5f5f5;
}

@charset "UTF-8";
.slide {
  width: calc(100% - 10px);
  margin: 20px auto 0;
  overflow: hidden;
  position: relative;
  /*overflow:hidden;隐藏不成功,尤其是圆角时;试试添加z-index:1;*/
  z-index: 1;
  background: #ccc;
  border-radius: 10px;
  -webkit-tap-highlight-color: transparent;
}
.slide .slide-group .slide-item {
    position: relative;
    float: left;
}
.slide .slide-group .slide-item a {
      overflow: hidden;
      display: block;
      width: 100%;
      text-decoration: none;
}
.slide .slide-group .slide-item a img {
        display: block;
        width: 100%;
}
.slide .dots {
    position: absolute;
    bottom: 10px;
    left: 0;
    right: 0;
    height: 6px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.slide .dots .dot {
      width: 6px;
      height: 6px;
      background: #fff;
      margin-right: 6px;
      border-radius: 6px;
      -webkit-transition: all 0.3s ease;
      transition: all 0.3s ease;
}
.slide .dots .dot.active {
        width: 12px;
        background: red;
}
.slide .dots .dot:last-child {
        margin-right: 0;
}
